.education {
    padding: 100px 0px;
    margin: 50px 0px;
    margin-left: 120px;
    background-color: rgba(136, 6, 6, 0.101);
  }
  
  .education hr {
    color: #971b0a;
    width: 400px;
    margin: 10px auto;
  }
  
  .education h2 {
    font-weight: bold;
    color: #971b0a;
  }
  
  .education .vertical-timeline-element-title {
    color: #971b0a;
  }
  
  .education .vertical-timeline-element-subtitle {
    color: #1e1e2c;
  }
  
 
   .education {
    background-color: rgba(230, 222, 222, 0.185);
  }
  
  
  @media (max-width: 600px) {
    .education {
      margin: 0;
      padding: 0;
      margin-top: 30px;
    }
    .education h2 {
      padding-top: 20px;
    }
    .education hr {
      width: 280px;
      margin-bottom: 10px;
    }
  }